body {
  font-family: monospace;
  background-color: #000;
  color: #fff;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.terminal {
  width: 60%;
  border: 1px solid #333;
  padding: 10px;
  background-color: #000;
  color: #fff;
  overflow-y: auto;
  white-space: pre-wrap;
  font-size: 1rem;
  height: 80%;
  border-radius: 10px;
}

/* Styling the scrollbar for WebKit-based browsers */
.terminal::-webkit-scrollbar {
  width: 12px;
}

.terminal::-webkit-scrollbar-track {
  background: #333;
}

.terminal::-webkit-scrollbar-thumb {
  background-color: #555;
  border-radius: 6px;
  border: 3px solid #333;
}

.terminal::-webkit-scrollbar-thumb:hover {
  background-color: #888;
}

/* Styling the scrollbar for Firefox */
.terminal {
  scrollbar-width: thin;
  scrollbar-color: #555 #333;
}

.output div {
  margin-top: 10px;
}

.command {
  display: block;
  color: #ffc90f;;
  margin-left: 10px;
}

.prefix-command{
  display: flex;
}

.result {
  display: block;
}

.inputbox {
  display: flex;
}

.prefix {
  user-select: none;
  /* background-color: #ffc90f; */
  color: #fff;
  display: flex;
  align-items: center;
}

.input {
  flex: 1;
  background-color: transparent;
  border: none;
  outline: none;
  font-family: inherit;
  font-size: inherit;
  padding: 0;
  color: #ffc90f;
  margin-left: 10px;
}

.textarea-box {
  display: flex;
}

.textarea-box textarea {
  color: #66ff00;
  text-decoration: none;
  border: none;
  outline: none;
  background-color: #000;
  font-size: 1rem;
  padding: 0;
  margin: 0;
  margin-left: 10px;
}

.pyboxInput {
  color: #41FDFE;
}

.pyboxError {
  color: #EE4B2B;
}